import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { Container } from 'react-bootstrap';
import { Link, useNavigate, useParams } from 'react-router-dom'

const Details = () => {
    let [prod, setProd] = useState(null);
    let [disable, setDisable] = useState(false);
    let { id } = useParams();
    let redirecting = useNavigate();

    useEffect(() => {
        axios.get(`http://localhost:4000/api/ecommerce/single/${id}`)
            .then(res => {
                setProd(res.data)
            })
            .catch(err => {
                console.log(err.message);
            })
    });

    function handleDelete(xid) {
        setDisable(true)
        axios.post('http://localhost:4000/api/ecommerce/deleteproduct', { id: xid })
            .then(resp => {
                redirecting('/');
            })
            .catch(err => {
                console.log(err.message);
            })
    }
    
  return (
      <div>
          {prod && <Container className='mt-4 text-center align-content-center justify-content-center'>
              <h1> {prod.prodName} </h1>
              <div className="text-center mt-4">
                  <img className='img-fluid w-100 h-100 my-5' src={prod.prodImg_url} alt={prod.prodName} />
              </div>
              <div className='text-center text-muted mx-3 p-3 lead lh-2 fs-4'>
                  <h1 className='text-primary fw-bold display-5'> ${prod.prodPrice} </h1>
                  <p> {prod.prodDetails} </p>
              </div>

              <div className='btn-group text-center'>
                  <Link to={'/'} className='btn btn-light btn-outline-primary'>Back</Link>
                  <Link to={`/update/${prod._id}`} className='btn btn-light btn-outline-success mx-3'>Update</Link>
                  <Link onClick={(e) => handleDelete(prod._id)}
                      disabled={disable}
                      className='btn btn-light btn-outline-warning'>Delete Blog</Link>
              </div>
          </Container>}
    </div>
  )
}

export default Details